.contactBlock {
    padding: 50px 0 50px 0;
    min-height: 100vh;
    background-color: green;
}
.contactsContainer {
    display: flex;
    flex-direction: column;
}
.contactForm {
    width: 280px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.contactForm textarea {
    resize: none;
    min-height: 100px;
}